.welcome_page {
  background-color: var(--warna-cream);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  color: var(--warna-biru);
  justify-content: center;
  text-transform: uppercase;
}
@media screen and (min-width: 30rem) {
  .welcome_page {
    font-size: 1.5rem;
  }
}
@media screen and (min-width: 40rem) {
  .welcome_page {
    font-size: 2rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .welcome_page {
    font-size: 2.5rem;
  }
}
.welcome_page .cursor {
  display: none;
  pointer-events: none;
}
@media screen and (min-width: 64rem) {
  .welcome_page .cursor {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    z-index: 100;
    border: var(--warna-biru) solid 1px;
  }
}
.welcome_page .container {
  width: 100%;
  height: 100%;
}
.welcome_page .container .selamat-datang {
  width: 100vw;
  height: calc(var(--vh, 1vh) * 100);
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.welcome_page .container .selamat-datang h2 {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  font-size: 18vw;
  overflow: hidden;
}
.welcome_page .home-page {
  width: 100vw;
  height: calc(var(--vh, 1vh) * 100);
  background-color: var(--warna-biru);
  text-transform: uppercase;
}
.welcome_page .home-page .nav-home-page {
  width: 100vw;
  position: absolute;
  font-size: 1vw;
  color: var(--warna-cream);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.welcome_page .home-page .nav-home-page .nav-kiri {
  padding: 3rem 0 3rem 3rem;
  margin-right: 1rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.welcome_page .home-page .nav-home-page .nav-kanan {
  padding: 3rem 3rem 3rem 0;
  margin-left: 1rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--warna-biru);
}
.welcome_page .home-page .home-page-container {
  height: calc(var(--vh, 1vh) * 100);
  display: flex;
}
.welcome_page .home-page .home-page-container .fotografer-section {
  font-size: 1.5vw;
  color: var(--warna-cream);
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}
.welcome_page .home-page .home-page-container .fullstack-section {
  font-size: 1.5vw;
  color: var(--warna-biru);
  background-color: var(--warna-cream);
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}